/* Start App design */
.App {
    padding: 20px;
}
/* End App design */

/* Start first section design */
.profile-section {
    display: flex;
    margin: 5% 0;
}
/* End first section design */

.profile-section article:first-child {
    margin-right: 5%;
}

p {
    font-size: 20px;
}

a {
    text-decoration: none;
}

a:hover {
    color: #ec7b73;
    text-decoration: underline;
}

/* Start second section design */
.testimonies {
    display: flex;
}
/* End second section design */

.studentImg {
    width: 25vw;
}

.testimonies article {
    width: 33%;
}

figcaption {
    text-align: center;
}

/* Start form style */
form {
    width: 550px;
    min-width: 400px;
    border: none;
    border-radius: 5px;
    margin: auto;
}
/* End form style */

.block-label, .block-input {
    display: block;
}

.block-label {
    margin: 8px 0;
}

.block-input {
    width: 80%;
    border-radius: 5px;
}

input[type='password'] {
    margin-bottom: 3%;
}

.remember-me {
    margin-left: 35%;
}

button {
    width: 20%;
    font-size: 17px;
    background-color: #6d6dff;
    border: none;
    border-radius: 5px;
    margin-left: 35%;
}

.mv-para-right {
    text-align: right;
    font-size: 15px;
    margin-top: 5%;
}